
import { defineComponent, PropType, ref, watch } from 'vue'
import { PropDataType, ComponentType } from '@/domains/dev/prop-config'
import {
  fontFamilys,
  fontWeights,
  fontStyles,
  hAligns,
  vAligns,
  writingModes,
  justifyContents,
  aligns,
  angles,
  locations,
  lineStyles,
  fillTypes,
} from '@/data/select-options'
import { boxImgs, decorateImgs } from '@/data/images'

export default defineComponent({
  name: 'PropComponent',
  props: {
    dataType: {
      type: String as PropType<PropDataType>,
      required: true,
    },
    componentType: {
      type: String as PropType<ComponentType>,
      required: true,
    },
  },
  emits: ['change'],
  setup(props, ctx) {
    const componentTypes = ref({ ...ComponentType })

    const strValue = ref('')
    const numValue = ref(0)
    const boolValue = ref(false)
    const arrValue = ref<string[]>([])

    watch(() => strValue.value, (nv: string) => {
      ctx.emit('change', nv)
    })

    watch(() => numValue.value, (nv: number) => {
      ctx.emit('change', nv)
    })

    watch(() => boolValue.value, (nv: boolean) => {
      ctx.emit('change', nv)
    })

    watch(() => arrValue.value, (nv: string[]) => {
      ctx.emit('change', nv)
    })

    return {
      componentTypes,
      strValue,
      numValue,
      boolValue,
      arrValue,

      fontFamilys,
      fontWeights,
      fontStyles,
      hAligns,
      vAligns,
      writingModes,
      justifyContents,
      aligns,
      angles,
      locations,
      lineStyles,
      fillTypes,
      boxImgs,
      decorateImgs,
    }
  },
})
